module.exports = "<div id=champions_intro_box _v-31f9af1e=\"\"><div class=intro_box _v-31f9af1e=\"\"><h2 _v-31f9af1e=\"\">03. THE CHAMPIONS</h2><div @click=show_methods() class=implementation_tag _v-31f9af1e=\"\"><svg width=24px height=24px viewBox=\"864 144 24 24\" class=implementation_symbol _v-31f9af1e=\"\"><g stroke=none stroke-width=1 fill=none fill-rule=evenodd transform=\"translate(864.000000, 144.000000)\" _v-31f9af1e=\"\"><path d=\"M11,17 L13,17 L13,11 L11,11 L11,17 L11,17 Z M12,2 C6.48,2 2,6.48 2,12 C2,17.52 6.48,22 12,22 C17.52,22 22,17.52 22,12 C22,6.48 17.52,2 12,2 L12,2 Z M12,20 C7.59,20 4,16.41 4,12 C4,7.59 7.59,4 12,4 C16.41,4 20,7.59 20,12 C20,16.41 16.41,20 12,20 L12,20 Z M11,9 L13,9 L13,7 L11,7 L11,9 L11,9 Z\" fill-opacity=0.7 fill=#2DAAB7 _v-31f9af1e=\"\"></path></g></svg><p class=methods_tag _v-31f9af1e=\"\">Methodology / Implementation Notes</p></div><div v-show=show_note class=implementation_box _v-31f9af1e=\"\"><svg @click=show_methods() width=14px height=14px viewBox=\"5 5 14 14\" class=exit_icon _v-31f9af1e=\"\"><polygon stroke=none fill=rgb(245,245,245) fill-rule=evenodd points=\"19 6.41 17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12\" _v-31f9af1e=\"\"></polygon></svg><h4 _v-31f9af1e=\"\">Methodology / Implementation Notes</h4><p _v-31f9af1e=\"\">Champion Mastery Score Rank is calculated by counting the number of summoners with Rank 5 mastery.</p><p _v-31f9af1e=\"\">The 'Champs Commonly Mastered' together section is calculated as follows: 1) get the % of summoners with Rank 5 mastery for every single champion, 2) for all summoners with Rank 5 mastery *for the selected champ*, get the % of summoners with Rank 5 mastery for every other champion, 3) get the % increase between the two ratios and sort.</p><p _v-31f9af1e=\"\">This approach beats a raw counts approach because for raw counts every champ would just show the most mastered champs (Vayne, Lee Sin, Thresh) rather than the champs it has special affinity for. The drawback here is that champs with lower sample size (i.e. Urgot, Galio) can have disproportionate ratios.</p></div><h4 _v-31f9af1e=\"\">Pick a champ in the grid to see which champs are mastered the most and which champs get mastered together.</h4><div class=selected_summoner_box _v-31f9af1e=\"\"><div class=champion_box _v-31f9af1e=\"\"><img :src=get_large_champion_image(currentChamp) class=selected_champion_image _v-31f9af1e=\"\"></div><div class=champ_content_box _v-31f9af1e=\"\"><h4 class=champion_name _v-31f9af1e=\"\">{{get_champion_name(currentChamp)}}</h4><div class=most_mastered_box _v-31f9af1e=\"\"><h6 _v-31f9af1e=\"\">Champion Mastery Score Rank</h6><h2 class=rank_tag _v-31f9af1e=\"\">{{get_champion_rank(currentChamp)}}/130</h2></div><div class=related_champs_box _v-31f9af1e=\"\"><h6 _v-31f9af1e=\"\">Champs Commonly Mastered With {{get_champion_name(currentChamp)}}</h6><div v-for=\"champion in champions_in_common[currentChamp].slice(0,12)\" class=related_champion_image_box _v-31f9af1e=\"\"><div class=related_champion_index _v-31f9af1e=\"\"><p _v-31f9af1e=\"\">{{$index + 1}}</p></div><img :src=get_champion_image(champion.champ) class=related_champion_image _v-31f9af1e=\"\"></div></div></div></div></div><div class=champion_images_box _v-31f9af1e=\"\"><div v-for=\"champion in champions\" @click=set_current_champ(champion) class=champion_image_box _v-31f9af1e=\"\"><img :src=get_champion_image(champion) class=champion_image _v-31f9af1e=\"\"></div></div></div>";


